body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
	display: inline-block;
	text-align: center;
	width: 100%;
	background-color: #610303;
	border-bottom: 6px solid #111;
}

header h1{
font-size: 3rem;
color: white;
margin: 0.2em;
}

.galerie{
	width: 90%;
	margin: 1% 3%;
	display: inline-block;
	background-color: #333;
	border-radius: 10px;
	padding: 1% 2%;
	position: relative;
}

.galeriaElemento{
	float:left;
	margin: 2%;
	background-color: #AAA;
	overflow: hidden;

}


.galeriaElemento:hover{
	cursor: pointer;
}

.newVentana{
	width: 100%;
	height: auto;
	position: relative;
	float: left;
}

.ventana{
	width: 72%;
	height: auto;
	border-radius: 10px;	
	background-color: #EEE;
	margin: 1% 18%;
    padding:1%;
    display: block;
    float: left;
}
.ventana_botones{
	width: 10%;
	display: flex;
	float: left;
	position: relative;
	margin-top: 13%;
}

.ventana_botones:hover{
	cursor: pointer;

}

.ventana_botones img {
	margin: auto;
	width: 30%;
}	

.ventana_title{
	width: 100%;
}

.ventana_seccion{
	padding: 0% 2%;
	display:block;
	float: left;
}

.ventana_cerrar{
	text-align: right;
    font-weight: 600;
}

.ventana_cerrar:hover{
	cursor: pointer;
}


@media only screen and (min-width: 1000px) {
	.galeriaElemento{
		width: 15%;
		height: 170px;	
	}	

	.ventana_seccion{
		width: 46%;
	}

	.ventana_cerrar{
		font-size: 1.5rem;
	}

	.tecnicas{
    	margin-left: 3%;
    	margin-top: 1%;
	}		

}	

@media only screen and (max-width: 768px) {
	.galeriaElemento{
		width: 46%;	
		height: 170px;
	}

	.ventana_seccion{
		width: 96%;
	}

	.ventana_cerrar{
		font-size: 2.2rem;
	}

	.tecnicas{
    	margin-left: 3%;
    	margin-top: 3%;
	}	

}

.tecnicas label{
  font-size: 1.5rem;
    margin-right: 2%;
}

#selectivo{
      padding: 6px;
    border: 2px solid #333;
    border-radius: 10px;
    font-size: 1rem;
    width: 50%;
    text-align: right;
}

#overBar{
	width: 14.5%;
	position: fixed; 
	display: none;
	background-color: rgba(99, 11, 11, 0.7);
	margin: 0;
	height: 40px
}
#overBar p{
	width: 100%; 
	text-align: center;
	color: white;
	font-weight: 600;
}	
